export default {
	data() {
		return {
			winSize:false,///* 显示遮罩 */
			windowHeight:0,//高度
			recordList:[{
				id:0,
				popu:false
			},{
				id:1,
				popu:false
			},{
				id:2,
				popu:false
			}]
		}
	},
	onLoad() {
		this.getWindowSize();
		
	},
	methods: {
		/* 获取窗口尺寸 */
		getWindowSize() {
			uni.getSystemInfo({
				success: (res) => {
					this.windowHeight = res.windowHeight
				}
			})
		},
		//点击删除
		onLongPress(key){
			var that = this
			that.recordList[key].popu = true
			that.winSize = true
			
		},
		
		//点击遮罩
		winSizeClick(){
			var that = this
			that.winSize = false
			that.recordList.forEach(function(item){
				item.popu = false
			})
		},
		
		//删除记录
		delListClick(){
			this.winSizeClick()
		},
		
	}
}
